.container-shopping-list {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.violet-color {
  color: #4f2ee8;
}

.shopping-main__title {
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

@media screen and (min-width: 768px) {
  .shopping-main__title {
    font-size: 48px;
    line-height: 1.08;
  }
}

.empty-list {
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  display: flex;
}

.shopping-empty-text {
  text-align: center;
  max-width: 349px;
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 1.33;
}

.shopping-empty-img {
  width: 265px;
  height: 198px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.shopping-list__page {
  width: 100%;
  padding-top: 40px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (min-width: 768px) {
  .shopping-empty-img {
    width: 322px;
    height: 241px;
  }

  .shopping-list__page {
    padding: 40px 0;
  }
}

